	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/3-hour-hacks/index.md absPath of file >>> MarkdownRemark","html":"<h2>Here’s my next big idea.</h2>\n<p>Most products seem to be an idea that you can knock up a prototype for in three hours, if you know what you’re doing.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-3a143.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 630px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 56.12499999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"1 aAX TP pQloG9lQfL4xIOw 2x\"\n        title=\"\"\n        src=\"/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-af57e.jpeg\"\n        srcset=\"/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-847f7.jpeg 158w,\n/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-93973.jpeg 315w,\n/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-af57e.jpeg 630w,\n/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-28cf9.jpeg 945w,\n/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-0d474.jpeg 1260w,\n/static/1*aAX-TP_pQloG9lQfL4xIOw@2x-2aced48c5bb865243256b26b4a0dcc0f-3a143.jpeg 1600w\"\n        sizes=\"(max-width: 630px) 100vw, 630px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Even if you <em>don’t</em> know what you’re doing you can learn an awful lot about a subject in that space of time.</p>\n<p>My ‘big idea’ is to time-box my side projects into three-hour hacks. I’ll work on an idea for three hours, with a focus on producing something new and unique, and then stop; document, and evaluate progress and likelihood of eventual success.</p>\n<p>Essentially it’s a bit of mental re-framing, a release from the pressure of creating a perfect product. I can always continue with a prototype if I like where it’s heading, or I can shelve it for some indeterminate point in the future.</p>\n<hr>\n<p>I’m reminded of <a href=\"https://news.ycombinator.com/item?id=8863\">this comment</a> from when Dropbox first came out;</p>\n<blockquote>\n<p><em>“For a Linux user, you can already build such a system yourself quite trivially by getting an FTP account, mounting it locally with curlftpfs, and then using SVN or CVS on the mounted filesystem. From Windows or Mac, this FTP account could be accessed through built-in software.”</em></p>\n</blockquote>\n<p>Sounds simple, doesn’t it? The key to success with a product is not whether it’s already achievable, its the other 99% – time and effort, dedication, marketing, infrastructure and a paying audience, to name a few.</p>\n<hr>\n<p>My motivation with side projects is to create something that I want, and it would be a nice bonus to have others want it also. I enjoy learning as I go and I don’t have lots of time and money and effort to expend pushing these projects mainstream, and I’m okay with that. If I can crack the core of the tech in three hours that’s enough of a win for me.</p>\n<p>3 Hour Hacks, here we come.</p>","frontmatter":{"title":"3 hour hacks","date":"March 11, 2018","tags":["Hackaday","Side Project","Inspiration"],"url":"https://medium.com/@psyked/3-hour-hacks-d22772439d04","image":"https://cdn-images-1.medium.com/max/1200/1*aAX-TP_pQloG9lQfL4xIOw@2x.jpeg","description":"Here’s my next big idea."}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/pokeproject-adventures-with-lego-r-bricks/","tags":["pokeproject","nuxt","lego"],"title":"PokéProject: Adventures with LEGO® Bricks","image":null,"date":"2017-10-05T20:28:11.000Z"}},"next":{"frontmatter":{"path":"/blog/building-duotone/","tags":["Design","Duotone","Side Project","Nuxtjs"],"title":"Building “duotone”","image":"https://cdn-images-1.medium.com/max/1200/1*1v5wB-0SlpzMcdxODc75Fw.png","date":"2018-03-13T23:22:11.253Z"}}}}

